body{
background-color: black;
}

.container{
  columns: 3;
  column-gap: 5px;
  width: 80%;
  margin: 2px auto;
}

.container img{
  width: 80%;
  margin: 0;
  padding: 5px;
}
.header{
  font-family: 'Times New Roman', Times, serif;
  font-weight: bolder;
}

span{
  color: green;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 45px;
  padding-left: 8%;
  padding-right: 8%;
}

.logo {
  color: white;
  font-size: 35px;
  letter-spacing: 1px;
  cursor: pointer;
  float: left;
}

span {
  color: #0d5f0d;
}

nav ul li {
  list-style-type: none;
  display: inline-block;
  padding: 10px 25px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  text-transform: capitalize;
}

nav ul li a:hover {
  color: #f9004d;
  transition: .4s;
}

.btn {
  background-color: #0d5f0d;
  color: white;
  text-decoration: none;
  border: 2px solid transparent;
  font-weight: bold;
  padding: 10px 25px;
  border-radius: 30px;
  transition: transform .4s;
}

.btn:hover {
  transform: scale(1.2);
}